import DraftsProvider from './drafts.provider';
import React from 'react';
import ReactDOM from 'react-dom';
import { utils } from 'digirati-annotation-plugin-core';

export default function DraftsPlugin(root, attributes, store) {
  const {
    label,
    filterBy,
    emptyState,
    hideIfEmpty,
    thumbnailSize,
    showThumbnail,
    description,
    ...unknownProps
  } = attributes;
  utils.unknownPropertyWarning(unknownProps);


  return ReactDOM.render((
    <DraftsProvider
      label={label || "Your annotations"}
      filterBy={filterBy}
      emptyState={emptyState}
      hideIfEmpty={hideIfEmpty}
      description={description}
      showThumbnail={showThumbnail === 'true'}
      thumbnailSize={thumbnailSize ? parseInt(thumbnailSize, 10) : null}
      store={store}
    />
  ), root);
}



