import React, {Component} from 'react';
import BEM from 'digirati-bem-js';
import './ComboButton.scss';

const $style = BEM.block('combo-button');
export default class ComboButton extends Component {

  state = {isOpen: false, isHovered: false };

  close = () => {
    this.setState({isOpen: false});
  }

  open = () => {
    this.setState({ isOpen: true });
  }

  render() {
    const {children, disabled = false, ...props} = this.props;
    const options = React.Children.map(children, (child, i) => i < 1 ? null : child);
    const label = React.Children.map(children, (child, i) => i < 1 ? child : null);
    return (
      <div className={$style.modifiers({ disabled })} onMouseLeave={this.close}>
        <div onMouseEnter={this.close} className={$style.element('button')}>
          <span className={$style.element('cta').modifiers({ single: options.length === 0 })} {...props}>
            {label}
          </span>
          { options.length ? (
            <span className={$style.element('toggle').modifiers({ 'open': this.state.isOpen })} onClick={this.open}>&rsaquo;</span>
          ) : null }
        </div>
        { options.length && this.state.isOpen ? (
          <ul className={$style.element('list')}>
            {options}
          </ul>
        ) : null}
      </div>
    );
  }
}

ComboButton.Option = ({ children, ...props }) => (
  <li className={$style.element('list-item')} {...props}>
    {children}
  </li>
);



