import React, { PureComponent } from 'react';
import BEM from 'digirati-bem-js';
import './Hidden.scss';


const style = BEM.block('hidden-input');

export default class Hidden extends PureComponent {

  componentDidMount() {
    const {metaData, update} = this.props;
    if (metaData.defaultValue) {
      update(metaData.defaultValue);
    }
  }

  render() {
    const { id, value, metaData, preview, update, displayFieldSelector, elementEvents } = this.props;

    return (
      <div className={style.modifier({ 'labeled-selector': (metaData.selector && metaData.title) })}>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
          : null }
        </label>
        <input id={id} type="hidden" value={value || ''}/>
      </div>
    );
    
  }

}



