import React, {PureComponent} from 'react';
import BEM from 'digirati-bem-js';
import moment from 'moment';


const $style = BEM.block('action-time-ago');
export default class ActionTimeAgo extends PureComponent {

  render() {
    const {prefix, className, subject, verb, time, showComma = true} = this.props;

    return (
      <span className={className ? className : $style}>
        { [prefix, verb, 'by', subject+(showComma ? ',' : null), moment(time).fromNow()].filter(e => e).join(' ') }
      </span>
    );
  }
}



