import { get, labelId, parseSelectorTarget } from '../helpers';
import {
  BODY_TYPE,
  CONFORMS_TO,
  DESCRIPTION,
  ID,
  INPUT_OPTIONS,
  INPUT_TYPE,
  INPUT_DEFAULT_VALUE,
  PURPOSE,
  RDFS_LABEL,
  SELECTOR,
  SELECTOR_VALUE,
  TITLE,
  TYPE,
  FIELD_GROUP,
} from '../vocab';

export default class Field {

  type = 'field';

  getReference(parent) {
    return { parentId: parent.id, ...this };
  }

  static getAutocompleteEndpoint(options) {
    if (!options) {
      return null;
    }
    return (
      options[0] ?
        options[0] :
        (
          options['@id'] ?
            options['@id'] :
            null
        )
    );
  }

  constructor(context, id, inputType, annotationMetaData, omekaMetaData, metaData) {
    this.context = context;
    this.id = id;
    this.inputType = inputType;
    this.annotationMetaData = annotationMetaData;
    this.omekaMetaData = omekaMetaData;
    this.metaData = metaData;
  }

  isValidTarget(target) {
    if (target === 'canvas') {
      return true;
    }
    return !(
      this.metaData.selector &&
      this.metaData.selector.toLowerCase() !== 'wholecanvasselector'
    );
  }

  static fromJsonLD(jsonLd, inheritedContext) {
    const $ = get(jsonLd, inheritedContext);
    const context = $('@context', inheritedContext);

    return new Field(
      context,
      $(ID),
      $(INPUT_TYPE, { [ID]: null })[ID],
      {
        options: $(INPUT_OPTIONS),
        autocomplete: Field.getAutocompleteEndpoint($(INPUT_OPTIONS)),
      },
      {
        type: $(TYPE),
        group: labelId(
          $(FIELD_GROUP),
        ),
        bodyType: labelId(
          $(BODY_TYPE),
        ),
        purpose: labelId(
          $(PURPOSE),
        ),
      },
      {
        title: $(RDFS_LABEL) || $(TITLE),
        description: $(DESCRIPTION),
        conformsTo: labelId(
          $(CONFORMS_TO),
        ),
        selector: $(SELECTOR) ? $(SELECTOR)[ID] : null,
        selectorValue: parseSelectorTarget($(SELECTOR_VALUE)),
        defaultValue: $(INPUT_DEFAULT_VALUE),
      },
    );
  }

}



