import React, { PureComponent } from 'react';
import { connector } from 'digirati-annotation-redux';
import { ActionTimeAgo } from 'digirati-annotation-components';
import ComboButton from 'digirati-annotation-components/es/components/inputs/ComboButton';
import {currentImageService} from 'digirati-annotation-redux/es/query/manifestQuery';
import { getAnnotationBody } from 'digirati-annotation-bridge/es/annotations';
import BEM from 'digirati-bem-js';
import './drafts.scss';

function getImageFromTarget(state, draft, size) {
  const imageService = currentImageService(state);
  if (imageService) {
    const prefix = imageService.split('info.json')[0];
    if (!draft || !draft.selector) {
      return null;
    }
    if (draft.selector.type.toLowerCase() === 'wholecanvasselector') {
      return `${prefix}full/${size},/0/default.jpg`;
    }
    return `${prefix}${draft.selector.x},${draft.selector.y},${draft.selector.width || size},${draft.selector.height || size}/${size},/0/default.jpg`;
  }
  return null;
}

const $style = BEM.block('drafts-list');
class Drafts extends PureComponent {

  getUserName(draft) {
    return draft.fingerprint.creator || 'unknown';
  }

  render() {
    const {
      state, 
      label, 
      description, 
      drafts, 
      selectScopedDraft, 
      currentDrafts, 
      moveForward, 
      reset, 
      filterBy, 
      emptyState, 
      hideIfEmpty, 
      showThumbnail = false,
      thumbnailSize = 150
    } = this.props;
    const draftList = drafts ? Object.values(drafts) : [];
    const filterByMultiple = filterBy.split(',');
    const filteredDraftLIst = draftList.length ? draftList.filter(draft => {
      return !(filterByMultiple && filterByMultiple.indexOf(draft.fingerprint.source) === -1);
    }) : [];
    if (filteredDraftLIst.length === 0 && hideIfEmpty) {
      return null;
    }

    return (
      <div className={$style}>
        { label ? (
          <h3 className={$style.element('label')}>{label}</h3>
        ) : null }
        { description ? (
          <p className={$style.element('description')}>{ description }</p>
        ) : null }
        <ul className={$style.element('list').modifiers({ hasThumbnail: showThumbnail, [`thumbnail-${thumbnailSize}`]: showThumbnail })}>
          { filteredDraftLIst.length ? filteredDraftLIst.map(draft => {
            const thumbnail = showThumbnail && getImageFromTarget(state, 
              draft, 40);

            return <li className={$style.element('list-item').modifier({
              'selected': (currentDrafts && draft.fingerprint && draft.fingerprint.scope && currentDrafts[draft.fingerprint.scope] === draft.id)
            })} key={draft.id} onClick={() => {
              // moveForward(draft.selector.source.scope);
              if (draft.fingerprint && draft.fingerprint.scope && draft.fingerprint.path) {
                if (draft.fingerprint.path.length) {
                  reset(draft.fingerprint.scope);
                  draft.fingerprint.path.forEach(forward => moveForward(draft.fingerprint.scope, forward));
                }
                selectScopedDraft(draft.fingerprint.scope, draft.id);
              }
            }}>
              { showThumbnail && thumbnail ? (
                <div className={$style.element('thumbnail')}>
                  <img src={getImageFromTarget(state, draft, thumbnailSize)} />
                </div>
              ) : null }
              <ActionTimeAgo verb="Created" subject={ this.getUserName(draft) } time={draft.fingerprint.created}/>
              { draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_CHANGED' ? (
                <span className={$style.element('unsaved')}>unsaved changes</span>
              ) : null }
            </li>
          }) : (
            <div className={$style.element('empty-state')}>
              {emptyState ? emptyState : 'Any annotations you mark as in progress will show here'}
            </div>
          ) }
        </ul>
      </div>
    );
  }
}

export default connector(Drafts);



