import React from 'react';
import { ConfirmationForm } from 'digirati-annotation-components';
import BEM from 'digirati-bem-js';
import './FullForm.scss';

const baseStyle = BEM.block('full-form');

export default function renderForm({
  currentResourceTemplate,
  resourceForm,
  currentDraft,
  previewDraft,
  updateDraft,
  editSelector,
  onSave,
  unpreviewDraft,
  currentSelector,
  isSelecting,
  isSelectingSecondary,
  renderResourceEditor,
  importCaptureModel,
  onSaveAsInProgress,
  goBack,
  editFieldSelector,
  immutableSelector,
  currentTree,
  elementEvents,
  hideTitle,
  enablePublishing,
  bem,
  ...props
}) {
  const style = bem ? bem : baseStyle;
  const isWholeCanvas = currentDraft && currentDraft.selector && currentDraft.selector.type && currentDraft.selector.type.toLowerCase() === 'wholecanvasselector';

  return (
    <div className={style}>
      {!hideTitle ? (
        <div>
          <h3 className={style.element('title')}>{currentResourceTemplate.title}</h3>
          {currentResourceTemplate.description ? (
            <p className={style.element('description')}>{currentResourceTemplate.description}</p>
          ) : null}
        </div>
      ) : null}
      { !isWholeCanvas && !immutableSelector && currentDraft.isPreviewing === false && currentSelector && currentSelector.source && currentSelector.source.template === currentResourceTemplate.id ? (
        <div className={style.element('selector-info')}>
          Please select a region on the page
        </div>
      ): null}
      { !isWholeCanvas && !immutableSelector && currentDraft.isPreviewing === false && currentDraft.selector && editSelector ? (
        <div className={style.element('selector-info').modifier('confirmed')}>
          You have selected a region, do  you want to <a onClick={editSelector}>change its position</a>
        </div>
      ): null }
      {currentDraft.template === currentTree ? (
        <div className={style.element('container')}>
          {goBack ? (
            <button onClick={e => { e.preventDefault(); goBack(); }} className={style.element('close-icon')}>
              &times;
            </button>
          ) : null}
          <ConfirmationForm
            draft={currentDraft}
            fields={Object.values(resourceForm.fields)}
            isSelecting={isSelectingSecondary || isSelecting}
            displayFieldSelector={editFieldSelector}
            elementEvents={elementEvents}
            onSaveAsInProgress={onSaveAsInProgress}
            onEdit={draft => unpreviewDraft(draft.id)}
            onSave={draft => onSave(draft)}
            previewDraft={previewDraft}
            importCaptureModel={importCaptureModel}
            renderResourceEditor={renderResourceEditor}
            updateField={updateDraft}
            enablePublishing={enablePublishing}
            {...props}
          />
        </div>
      ) : (
        <div>Form disabled, please complete other annotation</div>
      )}
    </div>
  );
}



