import TaggingAnnotationsProvider from './tagging-annotations.provider';
import React from 'react';
import { render } from 'react-dom';
import { utils } from 'digirati-annotation-plugin-core';

export default function TaggingAnnotationsPlugin(root, attributes, store) {
  const {
    entityTypes,
  } = attributes;

  const types = entityTypes.split(',').map(type => type.substring(7).toLowerCase());

  return utils.renderWhen(store, state => Object.keys(state.elucidate.annotations).length > 0, (
    <TaggingAnnotationsProvider
      store={store}
      entityTypes={types}
    />
  ), root);
}



