import React, { PureComponent } from 'react';
import BEM from 'digirati-bem-js';
import HelpTooltip from '../ui/HelpTooltip';
import debounce from 'lodash/debounce';
import './Autocomplete.scss';

const style = BEM.block('autocomplete');

const AUTOCOMPLETE_DEBOUNCE_DELAY = 250;
const KEYCODE_ESCAPE = 27;

interface AutocompleteValue {
  label: string,
  uri: string
}

export default class Autocomplete extends PureComponent {

  state = {
    suggestionListHidden: false,
    suggestions: [],
    label: '',
  };

  componentDidMount() {
    if (this.props.value && this.props.value.label) {
      this.setState(() => ({
        label: this.props.value.label
      }));
    }
  }

  fetchNewSuggestions = debounce(() => {
    const { suggestionProvider } = this.props;

    suggestionProvider(this.state.label)
    .then(suggestions => {
      this.setState({
        suggestions,
        suggestionListHidden: false,
      });
    });
  }, AUTOCOMPLETE_DEBOUNCE_DELAY);

  handleInputReference = (ref) => {
    this.input = ref;
  };

  handleCompletionSelection = (selection) => {
    this.updateValue({
      label: selection.label,
      uri: selection.uri,
    });

    this.setState({
      suggestionListHidden: true,
    });

    if (this.input) {
      this.input.focus();
    }
  };

  handleKeyUp = (evt) => {
    switch (evt.keyCode) {
      case KEYCODE_ESCAPE:
        this.setState({
          suggestionListHidden: true
        });
        break;
      default:
        this.fetchNewSuggestions();
        break;
    }
  };

  updateValue(value: AutocompleteValue) {
    this.setState({
      label: value.label,
    });

    const { update } = this.props;
    update({ label: value.label, url: value.uri });
  }

  render() {
    const { preview, id, metaData } = this.props;
    const { suggestionListHidden, label } = this.state;

    if (preview) {
      return <div className={style.element('preview')}>{label}</div>;
    }

    const suggestionList = <ul className={style.element('suggestions')}>
      {this.state.suggestions.map((suggestion: AutocompleteValue, key) => {
        return (
          <li key={key}
              onClick={e => this.handleCompletionSelection(suggestion)}>
            {suggestion.label}
          </li>
        );
      })}
    </ul>;

    return (
      <div className={style}>
        <label className={style.element('label')} htmlFor={id}>
            {metaData.title} {' '}
            { (metaData.selector && metaData.selectorValue) ?
                (<a onClick={(e) => {
                    e.preventDefault();
                    displayFieldSelector(id);
                }} href="#">edit</a>)
                : null }
            {metaData.description ? (<HelpTooltip>{metaData.description}</HelpTooltip>) : null}
        </label>
        <input id={id} type="text" className={style.element('input')} value={label}
               ref={this.handleInputReference}
               onChange={e => this.setState({ label: e.currentTarget.value })}
               onKeyUp={e => this.handleKeyUp(e)} />
        { suggestionListHidden ? '' : suggestionList}
      </div>
    );
  }
}



