import React, { Component } from 'react';
import BEM from 'digirati-bem-js';
import HelpTooltip from '../ui/HelpTooltip';
import './EmbeddedResourceEditor.scss';

const style = BEM.block('embedded-resource-editor');

export default class EmbeddedResourceEditor extends Component {

  state = { isLoaded: false, isLoading: false };

  renderCurrentValue = (value, props, style) => {
    const { renderResource } = this.props;
    if (renderResource) {
      return renderResource(value, props, style);
    }
    return (
      <div>{value.label ? value.label : value}</div>
    );
  };

  componentDidMount() {
    const { captureModel, importCaptureModel } = this.props;
    const { isLoading, isLoaded } = this.state;
    if (isLoading) return null;
    if (isLoaded) return null;
    this.setState({ isLoading: true });
    importCaptureModel(captureModel).then(resourceTemplate => {
      this.setState({ isLoaded: true, isLoading: false, captureModelId: resourceTemplate['@id'] });
    });
  }

  renderResourceEditor = ({ captureModel, postSave, props, style, onBack }) => {
    const { renderResourceEditor } = this.props;
    const { isLoading, isLoaded, captureModelId } = this.state;
    if (isLoading) return 'loading...';
    if (isLoaded) {
      return renderResourceEditor(captureModelId, postSave, props, style, onBack);
    }
  };

  render() {
    const { captureModel, value, metaData, id, displayFieldSelector, postSave, onBack } = this.props;
    return (
      <div className={style}>
        {metaData ? (
          <div>
            <label className={style.element('label')} htmlFor={id}>
              {metaData.title} {' '}
              {(metaData.selector && metaData.selectorValue) ?
                (<a onClick={(e) => {
                  e.preventDefault();
                  displayFieldSelector(id);
                }} href="#">edit</a>)
                : null}
              {metaData.description ? (<HelpTooltip>{metaData.description}</HelpTooltip>) : null}
            </label>
          </div>
        ) : null}
        {value ?
          this.renderCurrentValue(value, this.props, style) :
          this.renderResourceEditor({
            captureModel,
            postSave,
            props: this.props,
            style,
            onBack,
          })
        }
      </div>
    );
  }
}



