import React, {PureComponent} from 'react';
import './HelpTooltip.scss';
import BEM from 'digirati-bem-js';

const $style = BEM.block('help-tooltip');
export default class HelpTooltip extends PureComponent {
  render() {
    const {label, children} = this.props;
    return (
      <span className={$style}>
        <span className={$style.element('label')}>{ label ? label : null }</span>
        <span className={$style.element('tooltip')}>{ children }</span>
      </span>
    );

  }
}



