import React, {Component} from 'react';
import {connector} from 'digirati-annotation-redux';
import {isTaggedAs, annotationToTag, tagDetails} from './tagging.utils';

export class TaggingAnnotations extends Component {

    render() {
        const {entityTypes, ...props} = this.props;

        let annotations = props.regionAnnotations.concat(props.wholeCanvasAnnotations)
            .filter(isTaggedAs(entityTypes))
            .sort((a, b) => {
                let source = tagDetails(a).uri;
                let target = tagDetails(b).uri;

                return source.localeCompare(target);
            })
            .reduce((output, next) => {
                let slice = output.slice(-1);
                let prev = slice[0];

                if (prev) {
                    let prevUri = tagDetails(prev).uri;
                    let nextUri = tagDetails(next).uri;

                    if (prevUri !== nextUri) {
                        output.push(next);
                    }
                } else {
                    output.push(next);
                }

                return output;
            }, []);

        return ((
            <div>
                {annotations.map(annotationToTag)}
            </div>
        ))
    }
}

export default connector(TaggingAnnotations);


