import React, { Component, PropTypes } from 'react';
import Form from '../inputs/Form';
import BEM from 'digirati-bem-js';
import isEqual from 'lodash.isequal';
import ComboButton from '../inputs/ComboButton';

const baseStyle = BEM.block('full-form');

class ConfirmationForm extends Component {

  // propTypes = {
  //   draft: PropTypes.shape({
  //     id: PropTypes.string.isRequired,
  //     isPreviewing: PropTypes.bool.isRequired,
  //     input: PropTypes.object.isRequired,
  //   }),
  //   bem: PropTypes.object,
  //   nextLabel: PropTypes.string,
  //   editLabel: PropTypes.string,
  //   saveLabel: PropTypes.string,
  //   updateField: PropTypes.func.isRequired,
  //   onEdit: PropTypes.func,
  //   onSave: PropTypes.func,
  //   previewDraft: PropTypes.func.isRequired,
  // };

  static ONLINE = 0;
  static OFFLINE = 1;

  state = {
    onlineStatus: ConfirmationForm.ONLINE,
  };

  setOnline = () => {
    this.setState({ onlineStatus: ConfirmationForm.ONLINE });
  };

  setOffline = () => {
    this.setState({ onlineStatus: ConfirmationForm.OFFLINE });
  };

  componentDidMount() {
    this.setState({ onlineStatus: navigator.onLine ? ConfirmationForm.ONLINE : ConfirmationForm.OFFLINE });
    window.addEventListener('online', this.setOnline);
    window.addEventListener('offline', this.setOffline);
  }

  renderPublishButton(draft) {
    const {
      onSave,
      onSaveAsInProgress,
      onSaveAsIncomplete,
      enableLocalStorageSaving = false,
      enablePublishing = true
    } = this.props;
    if (!onSave) return null;

    if (this.state.onlineStatus === ConfirmationForm.OFFLINE) {
      if (!onSaveAsInProgress || enablePublishing === false) {
        return (
          <ComboButton disabled>
            Save
          </ComboButton>
        );
      }
      return (
        <ComboButton onClick={() => onSaveAsInProgress(draft)}>
          Save work as in progress
        </ComboButton>
      );
    }

    if (enablePublishing === false) {
      return (
        <ComboButton onClick={() => onSaveAsIncomplete(draft)}>
          Save
          { enableLocalStorageSaving && (
            draft.fingerprint.source === 'localStorage' ? (
              draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_CHANGED' ||
              draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_NEW'
            ) : true
          ) && onSaveAsInProgress ? (
            <ComboButton.Option onClick={() => onSaveAsInProgress(draft)}>
              Save on this computer
            </ComboButton.Option>) : null}
        </ComboButton>
      );
    }

    return (
      <ComboButton onClick={() => onSave(draft)}>
        Publish
        { enableLocalStorageSaving && (
          draft.fingerprint.source === 'localStorage' ? (
            draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_CHANGED' ||
            draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_NEW'
          ) : true
        ) && onSaveAsInProgress ? (
          <ComboButton.Option onClick={() => onSaveAsInProgress(draft)}>
            Save on this computer
          </ComboButton.Option>) : null}
        {(
          draft.fingerprint.source === 'elucidate' ? (
            draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_CHANGED' ||
            draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_NEW'
          ) : true
        ) && onSaveAsIncomplete ? (
          <ComboButton.Option onClick={() => onSaveAsIncomplete(draft)}>
            Save work as in progress
          </ComboButton.Option>) : null}
      </ComboButton>
    );
  }

  render() {
    const {
      // Variables.
      draft,
      fields,
      bem,
      nextLabel,
      editLabel,
      saveLabel,
      saveAsInProgressLabel,
      children,
      // Actions.
      updateField,
      onEdit,
      onSave,
      onSaveAsInProgress,
      onSaveAsIncomplete,
      previewDraft,
      ...props,
    } = this.props;

    const style = bem ? bem : baseStyle;
    const draftIsEmpty = draft ? (Object.values(draft.input).filter(e => e).length === 0) : true;
    const nextButtonProps = {};
    if (draftIsEmpty) {
      nextButtonProps.disabled = 'disabled';
    }

    return (
      <div className={style}>
        {children ? children : (
          <Form
            draft={draft}
            fields={fields}
            updateField={(input, value) => updateField(draft.id, { [input.id]: value })}
            {...props}
          />
        )}
        <div className={style.element('actions')}>
          {draft.isPreviewing ? (
            <div>
              {onEdit ? (
                <button className={style.element('back-btn').modifiers({ first: true, last: !!onSave, })}
                        onClick={() => onEdit(draft)}>
                  {editLabel ? editLabel : 'Edit'}
                </button>
              ) : null}
              {this.renderPublishButton(draft)}
            </div>
          ) : (
            <button className={style.element('action-btn').modifiers({ first: true, last: true })}
                    onClick={() => previewDraft(draft.id, draft)} {...nextButtonProps}>
              {nextLabel ? nextLabel : 'Next'}
            </button>
          )}
        </div>
      </div>
    );
  }
}

export default ConfirmationForm;



