import React, { PureComponent } from 'react';
import BEM from 'digirati-bem-js';
import EmbeddedResourceEditor from './EmbeddedResourceEditor';
import Autocomplete from './Autocomplete';
import HelpTooltip from '../ui/HelpTooltip';
import Portal from 'react-portal';
import './Fallback.scss';

const style = BEM.block('fallback-input-box');
class Fallback extends PureComponent {
  state = {
    previousValue: null,
    creatingNew: false,
    fallbackValue: null,
  };

  handleNotFound = () => {
    this.setState({
      previousValue: this.props.value,
      creatingNew: true,
    });
    this.props.update(null);
    this.portal.openPortal();
  };

  handleBack = (state) => {
    if (this.state.previousValue) {
      this.props.update(this.state.previousValue);
    }
    this.setState({ creatingNew: false, previousValue: null });
  };

  postSave = (prom, annotation) => {
    prom.then(val => {
      this.setState({ creatingNew: false, previousValue: null, fallbackValue: { url: val.payload.id, label: annotation.label } });
      this.props.update({ url: val.payload.id, label: annotation.label });
    });
  };

  setPortal = ref => this.portal = ref;

  render() {
    const { creatingNew, fallbackValue } = this.state;
    const { autocomplete, captureModel, value, preview, metaData, currentDraft, ...props } = this.props;

    if (preview || this.state.fallbackValue) {
      return (
        <div>
          {metaData ? (
            <div>{metaData.title}</div>
          ) : null }
          { value && value.url ? (
          <a href={value.url} target="_blank" className={style.element('entity')}>
            <div className={style.element('preview-label')}>{value.label}</div>
            <div className={style.element('preview-url')}>{value.url}</div>
          </a>
          ) : (
            <span className={style.element('empty')}>
              Nothing selected
            </span>
          )}
        </div>
      );
      return <div>{value.label} ({value.url})</div>;
    }

    return (
      <div className={style}>
        {metaData ? (
          <label className={style.element('label')} htmlFor={props.id}>
            {metaData.title} {' '}
            { (metaData.selector && metaData.selectorValue) ?
              (<a onClick={(e) => {
                e.preventDefault();
                displayFieldSelector(id);
              }} href="#">edit</a>)
              : null }
            {metaData.description ? (<HelpTooltip>{metaData.description}</HelpTooltip>) : null}
          </label>
        ) : null }

        <Autocomplete {...props} value={value} suggestionProvider={(value) => {
          const url = autocomplete['@id'] + '&q=' + encodeURIComponent(value);

          return fetch(url)
          .then(result => result.json())
          .then(response => response.completions);
        }}/>
        { currentDraft.selector ? (
          <a className={style.element('fallback-button')} onClick={e => this.handleNotFound()}>{ metaData.description || 'Can\'t find what you\'re looking for?' }</a>
        ) : null}
        <Portal ref={this.setPortal} isOpened={this.state.creatingNew}>
          <div className={style.element('portal')}>
            <div className={style.element('portal-inner')}>
              <EmbeddedResourceEditor {...props} currentDraft={currentDraft} captureModel={captureModel['@id']} postSave={this.postSave} onBack={this.handleBack}/>
            </div>
          </div>
        </Portal>
      </div>
    );
  }
}

export default Fallback;



