import React, { Component } from 'react';
import { connector } from 'digirati-annotation-redux';
import { Component as ResourceEditor } from 'digirati-annotation-plugin-resource-editor';
import { createDraftFromCaptureModel } from 'digirati-annotation-bridge';

class Tagging extends Component {

  state = {
    isAdding: false,
  };

  handleAdd = () => {
    const { createDraftFromCaptureModel, tree } = this.props;
    createDraftFromCaptureModel(tree, tree, {});

    this.setState({
      isAdding: true,
    });
  };

  render() {
    const { currentDraft, content, ...props } = this.props;
    const hasDraft = !!currentDraft;

    if (!currentDraft) {
      return ((
        <div>
          <button className="c-tag" disabled={hasDraft}
                  type="submit" onClick={this.handleAdd}>{content}
          </button>
        </div>
      ));
    }

    return ((
      <div>
        <ResourceEditor {...props} hideTitle={true} />

      </div>
    ));
  }
}
export default connector(Tagging, { createDraftFromCaptureModel });



