import TaggingProvider from './tagging.provider';
import React from 'react';
import { render } from 'react-dom';

import { importResourceTemplate } from 'digirati-annotation-bridge';


export default function TaggingPlugin($el, attributes, store) {
  const {
    canvas,
    manifest,
    resourceTemplates,
    target = 'canvas',
    entityTypes,
  } = attributes;

  const { dispatch } = store;

  const importCaptureModel = (resourceTemplates) => {
    return fetch(`${resourceTemplates}`)
    .then(r => r.json())
    .then(resourceTemplate => {
      dispatch(importResourceTemplate(resourceTemplate, target));
      return resourceTemplate;
    });
  };

  if (resourceTemplates) {
    return fetch(`${resourceTemplates}`)
    .then(r => r.json())
    .then(resourceTemplate => {
      dispatch(importResourceTemplate(resourceTemplate, target));
      return resourceTemplate;
    })
    .then(resourceTemplate => {
      render((
        <TaggingProvider
          store={store}
          target={target}
          manifest={manifest}
          canvas={canvas}
          tree={resourceTemplate['@id']}
          importCaptureModel={importCaptureModel}
          content={$el.innerText}
        />
      ), $el);
    });
  }
}


