import React, { PureComponent } from 'react';
import DatePickerComponent from 'react-datepicker';
import moment from 'moment';
import 'react-datepicker/dist/react-datepicker.css';
import HelpTooltip from '../ui/HelpTooltip';
import BEM from 'digirati-bem-js';
import './TextBox.scss';

const style = BEM.block('input-text-box');

class DatePicker extends PureComponent {

  // propTypes = {
  //   id: PropTypes.string.isRequired,
  //   value: PropTypes.string.isRequired,
  //   update: PropTypes.func.isRequired,
  //   metaData: PropTypes.shape({
  //     title: PropTypes.string.isRequired,
  //     description: PropTypes.string
  //   }).isRequired,
  //   preview: PropTypes.bool,
  // };

  handleChange(time) {
    this.props.update(time.toISOString());
  }

  state = {
    isStarted: false
  }

  componentDidMount() {
    if (this.props.value) {
      this.setState({ isStarted: true });
    }
  }

  onAddDate = () => {
    this.setState({ isStarted: true });
  }

  render() {
    const { isStarted } = this.state;
    const { id, value, metaData, preview, displayFieldSelector } = this.props;
    const time = moment(value);
    return (
      <div className={style}>
        <label className={style.element('label')} htmlFor={id}>
          {metaData.title} {' '}
          { (metaData.selector && metaData.selectorValue) ?
            (<a onClick={(e) => {
              e.preventDefault();
              displayFieldSelector(id);
            }} href="#">edit</a>)
            : null }
          { (isStarted) ?
            (<a onClick={(e) => {
              e.preventDefault();
              this.setState({ isStarted: false })
              this.props.update(null);
            }} href="#"> remove</a>)
            : null }
          {metaData.description ? (<HelpTooltip>{metaData.description}</HelpTooltip>) : null}
        </label>
        { preview ? (
          <div className={style.element('preview')}>{moment(value).format('LL')}</div>
        ) : (
          isStarted ? (
          <DatePickerComponent
            dateFormat="DD/MM/YYYY"
            className={style.element('input').modifier('datepicker')}
            selected={time.isValid() ? time : moment()}
            showYearDropdown={true}
            onChange={(e) => this.handleChange(e)}/>
        ) : (
          <div className={style.element('choice')} onClick={this.onAddDate}>Choose date</div>
        )) }
      </div>
    );
  }

}

export default DatePicker;



