import uuid from 'uuid/v4';
import { addAnnotation } from './annotations';
import { cancelSelector, chooseSelector, commitToCurrentDraft, editCurrentDraftSelector } from './selectors';
import { applyDefaultsToDraft, getCurrentResourceTemplate } from '../query/resourceQuery';
import { getCurrentDraft, getDraftById } from '../query/draftQuery';
import { selectorCompat } from '../utility';
import { withViewer } from './viewer';
import { isSelecting, getCurrentSelector } from '../query/selectorQuery';

export const DRAFT_CREATE = 'DRAFT_CREATE';
export const DRAFT_UPDATE_SELECTOR = 'DRAFT_UPDATE_SELECTOR';
export const DRAFT_REMOVE_SELECTOR = 'DRAFT_REMOVE_SELECTOR';
export const DRAFT_UPDATE_FIELD_SELECTOR = 'DRAFT_UPDATE_FIELD_SELECTOR';
export const DRAFT_REMOVE_FIELD_SELECTOR = 'DRAFT_REMOVE_FIELD_SELECTOR';
export const DRAFT_UPDATE_INPUT = 'DRAFT_UPDATE_INPUT';
export const DRAFT_SELECT = 'DRAFT_SELECT';
export const DRAFT_DESELECT = 'DRAFT_DESELECT';
export const DRAFT_PUBLISHING = 'DRAFT_PUBLISHING';
export const DRAFT_PUBLISHED = 'DRAFT_PUBLISHED';
export const DRAFT_PREVIEW = 'DRAFT_PREVIEW';
export const DRAFT_UNPREVIEW = 'DRAFT_UNPREVIEW';
export const DRAFT_CONFIRM = 'DRAFT_CONFIRM';
export const DRAFT_DISCARD = 'DRAFT_DISCARD';
export const DRAFT_IMPORT = 'DRAFT_IMPORT';

export const DRAFT_LIFECYCLE_NEW = 'DRAFT_LIFECYCLE_NEW';
export const DRAFT_LIFECYCLE_CHANGED = 'DRAFT_LIFECYCLE_CHANGED';
export const DRAFT_LIFECYCLE_READ = 'DRAFT_LIFECYCLE_READ';

export function generateDraftId() {
  return uuid();
}

export function createDraft(scope, emptyInput, template, motivation = 'tagging', selectors = null, draftDefaults = null, immutableSelector = null, path = []) {
  return (dispatch, getState) => {
    const id = uuid();
    const input = draftDefaults ? applyDefaultsToDraft(getState(), template, emptyInput, draftDefaults) : emptyInput;
    // Create draft.
    dispatch(createDirectDraft(scope, id, input, template, selectors, motivation, path));
    // And select it straight away.
    dispatch(selectDraft(scope, id));
    // Choose the selector attached to the draft.
    dispatch(chooseDraftSelector(scope, id, immutableSelector));
  };
}

export function chooseDraftSelector(scope, draftId, immutableSelector = null) {
  return (dispatch, getState) => {
    const currentTemplate = getCurrentResourceTemplate(getState(), scope);
    if (currentTemplate) {
      if (currentTemplate.metaData && currentTemplate.metaData.selector) {
        dispatch(chooseSelector(currentTemplate.metaData.selector, {
          template: currentTemplate.id,
          scope,
          draft: draftId,
        }, immutableSelector, null, !!immutableSelector));
      } else {
        dispatch(chooseSelector('WholeCanvasSelector', { template: currentTemplate.id, scope, draft: draftId }));
      }
    }
  };
}

function getValidLifecycle(desiredLifecycle, defaultLifecycle) {
  // desiredLifecycle must be one of these constants.
  return [
    DRAFT_LIFECYCLE_CHANGED,
    DRAFT_LIFECYCLE_NEW,
    DRAFT_LIFECYCLE_READ,
  ].indexOf(desiredLifecycle) === -1 ? (
    // If not, the default lifecycle.
    defaultLifecycle ?
      defaultLifecycle :
      // Defaulting to new.
      DRAFT_LIFECYCLE_NEW
    // If its valid, use it.
  ) : desiredLifecycle;
}

export function changeFingerPrintLifecyle({ fingerprint, ...draft }, desiredLifecycle) {
  const lifecycle = getValidLifecycle(desiredLifecycle, draft.lifecycle);

  return {
    ...draft,
    fingerprint: createFingerprint(
      fingerprint.scope,
      fingerprint.path,
      fingerprint.identity,
      fingerprint.created,
      lifecycle,
      fingerprint.source,
      fingerprint.creator,
    ),
  };
}

export function changeFingerPrintCreator({ fingerprint, ...draft }, creator) {
  return {
    ...draft,
    fingerprint: createFingerprint(
      fingerprint.scope,
      fingerprint.path,
      fingerprint.identity,
      fingerprint.created,
      fingerprint.lifecycle,
      fingerprint.source,
      creator,
    ),
  };
}

export function changeFingerprintSource({ fingerprint, ...draft }, source) {
  return {
    ...draft,
    fingerprint: createFingerprint(
      fingerprint.scope,
      fingerprint.path,
      fingerprint.identity,
      fingerprint.created,
      fingerprint.lifecycle,
      source,
      fingerprint.creator,
    ),
  };
}

export function changeFingerprintIdentity({ fingerprint, ...draft }, identity) {
  return {
    ...draft,
    fingerprint: createFingerprint(
      fingerprint.scope,
      fingerprint.path,
      identity,
      fingerprint.created,
      fingerprint.lifecycle,
      fingerprint.source,
      fingerprint.creator,
    ),
  };
}

export function createFingerprint(scope, path, identity, created = null, lifecycle = null, source = null, creator = null) {
  return {
    scope,
    path,
    identity,
    created: created ? created : (new Date()).toISOString(),
    lifecycle: lifecycle ? lifecycle : DRAFT_LIFECYCLE_NEW, // DRAFT_LIFECYCLE_NEW | DRAFT_LIFECYCLE_CHANGED
    source: source ? source : 'memory', // memory | elucidate | localStorage
    creator: creator ? creator : 'unknown',
  };
}

export function createDirectDraft(scope, id, input, template, selectors = null, motivation = 'tagging', path = [], created = null) {
  return {
    type: DRAFT_CREATE,
    payload: {
      id,
      input,
      template,
      selectors: selectors ? selectors : input,
      motivation,
      fingerprint: createFingerprint(scope, path, null, created, null, null, 'you'),
    },
  };
}

export function importDraft(id, draft) {
  return { type: DRAFT_IMPORT, payload: draft };
}

export function confirmDraft(scope) {
  return { type: DRAFT_CONFIRM, payload: { scope } };
}

export function deselectDraft(scope) {
  return { type: DRAFT_DESELECT, payload: { scope } };
}

export function discardDraft(scope, id) {
  return { type: DRAFT_DISCARD, payload: { id, scope } };
}

export function discardCurrentDraft(scope) {
  return (dispatch, getState) => {
    const currentDraft = getCurrentDraft(getState(), scope);
    if (currentDraft) {
      dispatch(discardDraft(scope, currentDraft.id));
      const selector = getCurrentSelector(getState());
      // @todo make sure this actually works or change selectors too.
      if (selector.source && selector.source.template === currentDraft.template) {
        dispatch(cancelSelector());
      }
    }
  };
}

export function removeSelectorFromDraft(id) {
  return { type: DRAFT_REMOVE_SELECTOR, payload: { id } };
}

export function removeSelectorFromCurrentDraft(scope) {
  return (dispatch, getState) => {
    const { id } = getCurrentDraft(getState(), scope);
    if (id) {
      dispatch(removeSelectorFromDraft(id));
    }
  };
}

export function markDraftAs(id, whenEqual, changeTo) {
  return (dispatch, getState) => {
    const state = getState();
    const draft = getDraftById(state, id);
    if (draft.fingerprint.lifecycle === whenEqual) {
      dispatch(importDraft(draft.id, changeFingerPrintLifecyle(draft, changeTo)));
    }
  }
}

export function updateDraft(id, fields) {
  return (dispatch, getState) => {
    markDraftAs(id, DRAFT_LIFECYCLE_READ, DRAFT_LIFECYCLE_CHANGED)(dispatch, getState);
    dispatch({ type: DRAFT_UPDATE_INPUT, payload: { id, fields } });
  }
}

export function addSelectorToDraftField(id, selector, fieldId) {
  return { type: DRAFT_UPDATE_FIELD_SELECTOR, payload: { id, selectors: { [fieldId]: selectorCompat(selector) } } };
}

export function editCurrentDraftFieldSelector(scope, fieldId, data) {
  return (dispatch, getState) => {
    const currentDraft = getCurrentDraft(getState(), scope);
    if (!currentDraft) {
      return;
    }
    if (!currentDraft.selectors || !currentDraft.selectors[fieldId]) {
      return;
    }
    const selector = {
      ...currentDraft.selectors[fieldId],
      ...data
    };
    dispatch(addSelectorToDraftField(currentDraft.id, selector, fieldId));
  };
}

export function addSelectorToDraftFieldFromViewer(id, props, fieldId, done) {
  return withViewer((Canvas, dispatch) => {
    const topLeft = Canvas.createRelativePoint(props.x, props.y);
    const bottomRight = Canvas.createRelativePoint(props.x + props.width, props.y + props.height);

    const selector = selectorCompat({
      type: props.type,
      x: topLeft.x,
      y: topLeft.y,
      width: bottomRight.x - topLeft.x,
      height: bottomRight.y - topLeft.y,
    });
    dispatch(addSelectorToDraftField(id, selector, fieldId));
    if (done) {
      done();
    }
  });
}

export function removeSelectorFromDraftField(id, fieldId) {
  return { type: DRAFT_REMOVE_FIELD_SELECTOR, payload: { id, fieldId } };
}

export function convertSelectorFromCanvas(Canvas, props) {
  if (!props.x && !props.y) {
    return { type: props.type || 'WholeCanvasSelector' };
  }
  const topLeft = Canvas.createRelativePoint(props.x, props.y);
  if (props.type !== 'boxSelector' && props.type !== 'madoc:boxdraw') {
    return selectorCompat({
      type: props.type,
      x: topLeft.x,
      y: topLeft.y,
    });
  }

  const bottomRight = Canvas.createRelativePoint(props.x + props.width, props.y + props.height);
  return selectorCompat({
    type: props.type,
    x: topLeft.x,
    y: topLeft.y,
    width: bottomRight.x - topLeft.x,
    height: bottomRight.y - topLeft.y,
  });
}

export function addSelectorToDraftFromViewer(id, props, done) {
  return withViewer((Canvas, dispatch) => {
    const selector = convertSelectorFromCanvas(Canvas, props);

    dispatch(addSelectorToDraft(id, selector));
    if (done) {
      done();
    }
  });
}

export function addSelectorToDraft(id, selector) {
  return (dispatch, getState) => {
    markDraftAs(id, DRAFT_LIFECYCLE_READ, DRAFT_LIFECYCLE_CHANGED)(dispatch, getState);
    dispatch({ type: DRAFT_UPDATE_SELECTOR, payload: { id, selector: selectorCompat(selector) } });
  }
}

export function selectDraft(scope, id) {
  return (dispatch, getState) => {
    markDraftAs(id, DRAFT_LIFECYCLE_NEW, DRAFT_LIFECYCLE_READ)(dispatch, getState);
    dispatch({ type: DRAFT_SELECT, payload: { id, scope } });
  }
}

export function previewDraft(id) {
  return (dispatch, getState) => {
    const state = getState();
    if (isSelecting(state)) {
      dispatch(commitToCurrentDraft(
        true,
        /* state.selector.currentSelector.type.toLowerCase() === 'wholecanvasselector', */
      ));
    }
    dispatch({ type: DRAFT_PREVIEW, payload: { id } });
  };
}

export function unpreviewDraft(scope, id, editSelector = false) {
  return (dispatch, getState) => {
    dispatch({ type: DRAFT_UNPREVIEW, payload: { id } });
    if (editSelector) {
      const state = getState();
      const currentDraft = getCurrentDraft(state, scope);
      if (
        currentDraft.id === id &&
        currentDraft.selector &&
        currentDraft.selector.type &&
        currentDraft.selector.type.toLowerCase() !== 'wholecanvasselector'
      ) {
        dispatch(editCurrentDraftSelector(scope));
      }
    }
  };
}

export function asyncPublishDraft(scope, id, promise) {
  return (dispatch) => {
    dispatch({ type: DRAFT_PUBLISHING, payload: { id } });
    return promise.then((result) => {
      dispatch(publishDraft(scope, id));
      return result;
    });
  };
}

export function publishDraft(scope, id) {
  return (dispatch, getState) => {
    markDraftAs(id, DRAFT_LIFECYCLE_CHANGED, DRAFT_LIFECYCLE_NEW)(dispatch, getState);
    dispatch(addAnnotation(getState().drafts.list[id]));
    dispatch({ type: DRAFT_PUBLISHED, payload: { id, scope } });
  };
}



