import React from 'react';
import {render} from 'react-dom';
import {createStore, reducers} from 'digirati-annotation-redux';
import {unknownPropertyWarning} from './core.utils';
import CoreProvider from './core.provider';
import localstorage from 'store';
import moment from 'moment';

export default function CorePlugin($el, {
  manifest,
  canvas,
  elucidateServer,
  locale,
  ...unknownProps
}) {
  unknownPropertyWarning(unknownProps);

  if (locale) {
    moment.locale(locale);
  }

  const url = window.location.href;
  const savedDraftList = localstorage.get(`annotation-studio/${url}`) || {};

  const store = createStore(reducers);
  render(
    <CoreProvider
      store={store}
      manifest={manifest}
      savedDraftList={savedDraftList}
      elucidateServer={elucidateServer}
      canvas={canvas}
    />
    , $el);

  return Promise.resolve(store);
}



