import React, {PureComponent} from 'react';
import {Provider} from 'react-redux';
import TaggingAnnotations from './tagging-annotations.component';

class TaggingAnnotationsProvider extends PureComponent {

    render() {
        const {store, ...props} = this.props;
        return (
            <Provider store={store}>
                <TaggingAnnotations {...props} />
            </Provider>
        );
    }
}

export default TaggingAnnotationsProvider;



