import React, { Component } from 'react';
import { connector } from 'digirati-annotation-redux';
import { changeFingerprintSource, changeFingerprintIdentity, changeFingerPrintCreator } from 'digirati-annotation-redux/es/actions/drafts';
import { getAllAnnotationsFromCollection } from 'digirati-annotation-redux/es/lib/elucidate/index';
import { asyncForEach } from './core.utils';
import 'whatwg-fetch';

class Core extends Component {

  static ONLINE = 0;
  static ONLINE_NOTIFICATION = 1;
  static OFFLINE = 2;

  state = {
    onlineStatus: Core.ONLINE
  };

  setOnline = () => {
    this.setState({ onlineStatus: Core.ONLINE_NOTIFICATION });
    setTimeout(() => {
      this.setState({ onlineStatus: Core.ONLINE });
    }, 2000)
  };

  setOffline = () => {
    this.setState({ onlineStatus: Core.OFFLINE });
  };

  warnUser = (event) => {
    const {drafts} = this.props;
    const changes = drafts ? Object.values(drafts).filter(draft => draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_CHANGED') : [];
    if (changes.length) {
      const message = `You have ${changes} unsaved annotation${changes.length > 1 ? 's' : null}, if you leave the page they will be lost.`;;
      event.preventDefault();
      event.returnValue = message;
      return message;
    }
    return null;
  };


  componentDidMount() {
    const {
      setServer,
      canvas: canvasId,
      manifest,
      createCollection,
      addElucidateAnnotation,
      setResource,
      elucidateServer,
      addManifest,
      selectManifest,
      selectCanvas,
      importDraft,
      savedDraftList,
    } = this.props;

    const subject = canvasId ? canvasId : manifest;

    if (!elucidateServer) {
      return;
    }
    if (canvasId) {
      fetch(manifest)
        .then(response => response.json())
        .then(response => addManifest(manifest, response))
        .then(response => selectManifest(manifest))
        .then(response => canvasId ? selectCanvas(canvasId) : null);
    }
    // Set the server.
    setServer(elucidateServer, subject);
    // Set the resource.
    if (subject) {
      setResource(subject);
    }

    window.addEventListener('online',  this.setOnline);
    window.addEventListener('offline', this.setOffline);
    window.addEventListener('beforeunload', this.warnUser);

    this.setState({ onlineStatus: navigator.onLine ? Core.ONLINE : Core.OFFLINE });

    // Import local storage saved items
    if (savedDraftList && Object.keys(savedDraftList).length) {
      Object.values(savedDraftList).forEach(
        drafts => Object.values(drafts).forEach(
          draft => {
            importDraft(draft.id, changeFingerPrintCreator(draft, 'you'))
          }
        )
      );
    }

    const isSavedDraft = (annotation) => {
      if (annotation.motivation !== 'http://www.digirati.com/ns/crowds#drafting') {
        return false;
      }
      if (Array.isArray(annotation.body)) {
        return false;
      }
      return (
        annotation.body.purpose === 'editing'
      );
    };

    // Set collection.
    createCollection(subject, subject)
      .then(c => getAllAnnotationsFromCollection(c))
      .then(iterable =>
        asyncForEach(iterable, i => {
            if (isSavedDraft(i)) {
              const draft = JSON.parse(i.body.value);
              const identity = i.id.substr(i.id.length-1) === '/' ? i.id.substr(0, i.id.length-1).split('/').pop() : i.id.split('/').pop();
              if (draft) {
                importDraft(draft.id, changeFingerPrintCreator(changeFingerprintIdentity(draft, identity), (i.creator && i.creator.name) ? i.creator.name : 'unknown'));
              }
            } else {
              addElucidateAnnotation(i);
            }
          }
        ),
      );
  }

  renderOffline() {
    const {drafts} = this.props;
    const changes = drafts ? Object.values(drafts).filter(draft => draft.fingerprint.lifecycle === 'DRAFT_LIFECYCLE_CHANGED') : [];
    return (
      <div className={'offline-notice'}>
        This page is currently offline. {` `}
        { changes.length ? (
          <span>
            If you leave this page you may lose your progress on {changes.length} annotation{changes.length > 1 ? 's' : null}
          </span>
          ) : null}
      </div>
    );
  }

  renderOnline() {
    return (
      <div className={'online-notice'}>
        Internet connection restored
      </div>
    );
  }

  render() {
    const {onlineStatus} = this.state;

    return (
      <div>
        { onlineStatus === Core.OFFLINE ? (this.renderOffline()) : null}
        { onlineStatus === Core.ONLINE_NOTIFICATION ? (this.renderOnline()) : null}
        <span style={{ display: 'none' }}>Annotation studio is running on this page. React {React.version}</span>
      </div>
    );
  }
}

export default connector(Core);



