import BEM from 'digirati-bem-js';
import React, {PureComponent} from 'react';
import './WholeCanvasSelector.scss';

const style = BEM.block('whole-canvas-selector');

class WholeCanvasSelector extends PureComponent {

  // propTypes = {
  //   skipConfirm: PropTypes.bool,
  //   onSave: PropTypes.func.isRequired,
  //   onCancel: PropTypes.func, // unused
  // };

  componentDidMount() {
    const { skipConfirm = true } = this.props;
    if (skipConfirm) {
      this.props.onSave();
    }
  }

  render() {
    return (
      <div />
    );
  }
}

export default WholeCanvasSelector;



