import { castBool, castId, get, labelId, motivationFromForm, warning } from '../helpers';
import {
  TYPE,
  COMBINE,
  DESCRIPTION,
  EXTERNALISE,
  HAS_PART,
  HUMAN_READABLE,
  ID,
  LABEL_PARTS,
  MOTIVATED_BY,
  MULTIPLE, OMEKA_LABEL,
  SELECTOR,
  SERIALIZE,
  TITLE,
  UI_GROUP,
  UI_CHOICE,
} from '../vocab';
import Field from './Field';

export default class CaptureModel {

  type = 'captureModel';

  static fromJsonLD(jsonLd) {
    const asArray = (a) => Array.isArray(a) ? a : [a];
    const $ = get(jsonLd);

    if ($(HAS_PART) === undefined) {
      warning('CaptureModel', $(ID), `No fields found in this form, expected in field ${HAS_PART}`);
    }
    if ($(TITLE) === undefined) {
      warning('CaptureModel', $(ID), `No title found in this form, expected in field ${TITLE}`);
    }

    return new CaptureModel(
      $('@context'),
      $(ID),
      $(TITLE),
      $(DESCRIPTION),
      {
        combine: castBool($(COMBINE, true)),
        externalise: castBool($(EXTERNALISE, true)),
        humanReadable: castBool($(HUMAN_READABLE, true)),
        serialize: castBool($(SERIALIZE)),
        multiple: castBool($(MULTIPLE)),
        // selectorValue: $(),
        selector: $(SELECTOR) ? castId($(SELECTOR), '', $('@context')).toLowerCase() : null,
        motivatedBy: motivationFromForm(
          labelId($(MOTIVATED_BY)),
        ),
        labelParts: labelId($(LABEL_PARTS)),
      },
      asArray($(HAS_PART)).map(
        field => Field.fromJsonLD(field, $('@context')),
      )
    );
  }

  isValidTarget(target) {
    if (target === 'canvas') {
      return true;
    }
    return (
      (!this.metaData.selector ||
      this.metaData.selector.toLowerCase() === 'wholecanvasselector') && (
        this.fields.reduce((s, field) => {
          if (field.isValidTarget(target)) {
            return s;
          }
          return false;
        }, true)
      )
    );
  }

  flatten() {
    const { fields, ...item } = this;
    return [item, ...this.dereferenceFields(fields)];
  }

  dereferenceFields(fields) {
    return fields.map(field => field.getReference(this));
  }

  constructor(context, id, title, description, metaData, fields) {
    this.context = context;
    this.id = id;
    this.title = title;
    this.description = description;
    this.metaData = metaData || {};
    this.fields = fields;
  }

  static is(jsonLd) {
    const $ = get(jsonLd);


    if (castId($(UI_GROUP), false, $('@context')) === 'madoc:form') {
      if (castBool($(UI_CHOICE), false)) {
        const firstChild = Array.isArray($(HAS_PART)) ? $(HAS_PART)[0] : $(HAS_PART);
        const firstChildIsField = !!firstChild['crowds:uiInputType'];
        warning('CaptureModel', $(ID), `we found the following conflicts:
  - This model is marked as a madoc:form
  - First child ${firstChildIsField ? 'IS' : 'IS NOT'} detected as a form field
  - The model is marked as a choice (crowds:uiChoice), which is incompatible with forms
  
We've continued under the assumtion that this is a form. 
Please update the model to get rid of this warning.
`);

      }
      return true;
    }

    // @todo remove temporary fix.
    console.warn('CaptureModel:', 'please use JSON-LD fields so that @id is equal to the QName.');
    return $(UI_GROUP) ? $(UI_GROUP)[OMEKA_LABEL] === 'madoc:form' : false;
  }

}



